* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    font-family: $font-family;
    line-height: 1.3em;
    margin: 0 auto;
    height: 100vh;
    padding: 20px;
    text-align: center;
}

.logo {
    padding: 5px;
    width: 4em;
}

h1 {
    flex: 1;
    font-size: 1.5rem;
    font-weight: 300;
    padding: 10px 0;
    margin: 10px 0;

    span {
        font-weight: 700;
    }
}

p {
    font-weight: 600;
    margin: 10px 0;
    font-size: 1rem;
    color: $dkGray;
}

.input{
  margin: 20px 0;
}

.form-control {
    margin: 20px 0 30px 0;

    input {
        padding: 15px;
        margin: 10px 0;
        width: 100%;
        border-radius: 50px;
        border: solid 1px $paleBlue;

        &::placeholder {
            color: $paleBlue;
        }
    }

    #message {
        font-size: 0.7em;
        font-style: italic;
        font-weight: 600;
    }

    button {
        text-decoration: none;
        font-weight: 600;
        background: $blue;
        color: $white;
        width: 100%;
        text-align: center;
        padding: 15px 70px;
        border: none;
        border-radius: 50px;
        box-shadow: 2px 2px 5px 2px $shadow;
    }
}

.hero-image {
    width: 100%;
    margin-bottom: 50px;

    @media only screen and (min-width: 620px) {
        width: 80%;
    }
}

footer {
    //text-align: center;
    margin-bottom: 1px;
    padding: 10px;
    .icons {
        margin: 10px;

        i {
            padding: 10px;
            border: solid 1px $paleBlue;
            border-radius: 100%;
        }
    }
    p {
        font-size: 0.9rem;
        color: $ltGray;
    }
}

.fa {
    color: $blue;
}

//Responsiveness

@media only screen and (min-width: 620px) {
    h1 {
        font-size: 3em;
        padding: 20px 0;
    }

    p {
        font-size: 1.5em;
        font-weight: 300;
    }

    .form-control {
        display: flex;
        align-items: baseline;
        justify-content: center;
        //padding: 0 100px ;

        .input {
            flex-basis: 70%;
            display: block;
            margin-right: 20px;
          

            input {
                &::placeholder {
                    font-size: 1.3em;
                }
            }
        }

        button {
            padding: 15px 20px;
            flex-basis: 20%;
        }
    }
}
